<template>
	<div class="login">
	  <div class="signin">
		<div style="width:138px;margin:0px auto;">
		  <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px"
			y="0px" viewBox="0 0 200 66.92" style="enable-background: new 0 0 200 66.92;" xml:space="preserve">
			<image style="overflow: visible;" width="1772" height="506"
			  transform="matrix(0.1071 0 0 0.1071 5.1307 5.6736)" xlink:href="@/assets/Definer_logo.png"></image>
		  </svg>
		</div>
		<h3 style="text-align:center;font-weight:800;margin-top:10px;">Welcome to Definer</h3>
  
		<div v-if="list.length === 0 || typeof item.id === 'undefined'">
		  <p style="color:black;">Choose your design preference for the project</p>
		</div>
  
		<div v-else>
		  <div v-for="item in list" :key="item.id">
			<p v-if="item.id">{{ 'Choose your design preference for project ' + item.id }}</p>
			<p v-else>Choose your design preference for the project</p>
		  </div>
		</div>
  
		<el-button style="margin-top:20px;" type="primary" class="submit" :loading="submitting"
		  @click="submit(1)">Begin</el-button>
	  </div>
	</div>
  </template>
  
  

<script>
export default {
	name: 'login',
	data() {
		return {
			lang: {},
			account: '',
			password: '',
			remenberPassword: true,
			submitting: false,
			list: [], // Add 'list' property
			loading: false // Add 'loading' property
		}
	},
	created() {
		// 多语言支持
		this.lang = this.$lang('login');
		this.getList();
	},
	methods: {
		submit() {
			this.$router.push({ path: '/u.library', query: this.$route.query });
			return;
		},
		getList() {
			this.loading = true;
			const groupId = this.$route.query.id;
			this.$ajax.get(`/project/get_board_name/?id=${groupId}`).then((r) => {
				// Process the retrieved data
				this.list = r.map((item) => {
					item.loading = true;
					return item;
				});
				this.loading = false; // Set loading to false after data retrieval
			}).catch((error) => {
				console.error(error);
				this.loading = false; // Set loading to false in case of an error
			});
		}
	}
}
</script>

<style lang="less" scoped>
@import url('../tools/common.less');

.login {
	width: 100%;
	height: 100%;
	position: absolute;
	//background: url('@{imgurl}/img/login-bg.jpg') #ffffff;
	//background-size: cover;
	//background-position: center center;
	background-color: #355CA1;

	.signin {
		width: 492px;
		margin: 200px auto 0px auto;
		background-color: #FFFFFF;
		border-radius: 24px;
		padding: 30px;

		p {
			color: #86868B;
			text-align: center;
			margin-top: 12px;
		}

		.form {
			margin: 15px 0px;
		}

		label {
			margin: 20px 0px 0px 0px;
			display: block;
		}

		.submit {
			width: 100%;
			background-color: #345CA0;
		}

		span {
			color: #345CA0;
			cursor: pointer;
		}
	}

	.box {
		width: 600px;
		height: calc(100% - 120px);
		padding-top: 120px;
		position: absolute;
		top: 0;
		right: 0;
		background: #ffffff;
		box-shadow: 0 0 30px 0 rgba(65, 95, 217, 0.3);

		.title {
			width: 400px;
			margin: 10px auto;
			font-size: 30px;
			color: #555555;
		}

		.form {
			width: 400px;
			margin: 100px auto;

			input {
				width: 100%;
				margin: 10px 0;
			}

			.form_foot {
				width: 100%;
				display: flex;
				margin-top: 10px;
				justify-content: space-between;
				color: #666666;

				span {
					cursor: pointer;
				}
			}
		}

		.submit {
			width: 400px;
			display: block;
			margin: 0 auto;
		}

		.reg {
			margin-top: 20px;
		}
	}
}
</style>